import Vue from "vue";
import VueRouter from "vue-router";
//  meta 
// hidden     boolean
// title      string
// clickOne    boolean
// keepAlive  boolean
// role 
const routes = [
  {
    path: "/",
    redirect: "/login",
    meta: {
      hidden: false
      // clickOne: true,
      // title: ""
    }
  },
  {
    path: "/homepage",
    name: "MainPage",
    component: () => import("../components/Index"),
    redirect: "/homepage",
    meta: {
      hidden: false,
      clickOne: true,
      title: ""
    },
    children: [
      {
        path: "/homepage",
        name: "HomePage",
        component: () => import("../components/exhibition/HomePage"),
        meta: {
          needLogin: false,
          hidden: false,
          keepAlive: true,
          title: ""
        }
      }
    ]
  },
  {
    path: "/mappage",
    component: () => import("../components/Index"),
    name: "MapPage",
    redirect: "/mappage",
    meta: {
      hidden: false,
      clickOne: true,
      title: ""
    },
    children: [
      {
        path: "/mappage",
        component: () => import("../components/exhibition/MapPage"),
        name: "MapPage",
        meta: {
          needLogin: false,
          hidden: false,
          keepAlive: true,
          title: ""
        }
      }
    ]
  },
  {
    path: "/datapage",
    component: () => import("../components/Index"),
    name: "DataPage",
    redirect: "/datapage",
    meta: {
      hidden: false,
      clickOne: true,
      title: ""
    },
    children: [
      {
        path: "/datapage",
        component: () => import("../components/exhibition/DataPage"),
        name: "DataPage",
        meta: {
          needLogin: false,
          hidden: false,
          keepAlive: true,
          title: ""
        }
      }
    ]
  },
  {
    path: "/user",
    component: () => import("../components/Index"),
    name: "user",
    redirect: "/user",
    meta: {
      hidden: false,
      clickOne: false,
      title: ""
    },
    children: [
      {
        path: "/user",
        component: () => import("../components/User"),
        name: "user",
        meta: {
          needLogin: false,
          hidden: false,
          title: "",
          keepAlive: false
        }
      }
    ]
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../components/Login"),
    meta: {
      title: ""
    }
  }
];
Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  routes
});

const VueRouterPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(to) {
  return VueRouterPush.call(this, to).catch(err => err);
};

export default router;



